@charset "utf-8";
.aboutTitle {
  height: 50px;
  font-weight: 600;
  font-size: 18px;
  border-bottom: 1px solid #ccc;
  margin-bottom: 20px;
}
.aboutTitle div {
  float: left;
  height: 100%;
  line-height: 50px;
  border-bottom: 3px solid #282c34;
  padding: 0 14px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  letter-spacing: 2px;
}
.author {
  overflow: hidden;
  height: 100%;
}
.author .head {
  width: 200px;
  height: 100%;
  float: left;
}
.author .head img {
  width: 100%;
  border-radius: 50%;
}
.author .introduce {
  margin-left: 250px;
  height: 100%;
  font-size: 14px;
  overflow: hidden;
}
.author .introduce .basic {
  height: 40px;
  text-align: left;
}
.author .introduce .basic div {
  width: 20%;
  float: left;
  height: 100%;
  line-height: 40px;
}
.author .tag {
  line-height: 30px;
  margin-top: 6px;
}
.about > div {
  margin-bottom: 20px;
}
#test {
  width: 1000px;
  height: 500px;
  margin-top: 50px;
}



