@charset "utf-8";
.header {
  height: 62px;
  background: rgba(0, 0, 0, 0.8);
  line-height: 62px;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
}
.header nav {
  width: 1200px;
  margin: 0 auto;
  overflow: hidden;
  height: 100%;
}
.header a {
  color: #e6e6e6;
  display: block;
  width: 120px;
  height: 100%;
  float: left;
  text-align: center;
  margin-right: 10px;
  font-size: 18px;
  letter-spacing: 1px;
  text-decoration: none;
}
.header a:hover {
  background: #4c4f54;
}
.header a i {
  margin-right: 4px;
}
.header .NavActive {
  background: #4c4f54;
}
.audio {
  position: fixed;
  right: 30px;
  bottom: 60px;
  border-radius: 27px;
  -webkit-box-shadow: 0px 0px 20px #666;
          box-shadow: 0px 0px 20px #666;
}



