@charset "utf-8";
.top {
  overflow: hidden;
  height: 300px;
}
.top .left {
  float: left;
  width: 700px;
  height: 100%;
}
.top .left .ant-carousel .slick-slide {
  text-align: center;
  height: 300px;
  line-height: 160px;
  background: #364d79;
  overflow: hidden;
}
.top .left .ant-carousel .slick-slide h3 {
  color: #fff;
}
.top .right {
  margin-left: 720px;
  background: #00a67c;
  height: 100%;
}
.bottom {
  margin-top: 20px;
}
.bottom .left {
  float: left;
  width: 900px;
}
.bottom .left .tab {
  height: 260px ;
}
.bottom .left .wzlist {
  height: 800px;
  margin-top: 20px;
}
.bottom .right {
  margin-left: 920px;
}
.bottom .right > div {
  margin-bottom: 20px;
  background: #fff;
}
.loading {
  text-align: center;
  line-height: 100px;
  font-size: 20px;
  color: red;
}
.noData {
  line-height: 200px;
  color: #00a67c;
  font-size: 24px;
  text-align: center;
}
.quick button {
  width: 31%;
  margin: 0 1%;
}
.homeTitle {
  color: #484848;
  font-size: 18px;
  border-left: 4px solid #484848;
  padding-left: 20px;
  font-weight: 600;
  letter-spacing: 2px;
  margin-bottom: 10px;
  background: #dadada;
}
.list {
  background: #fff;
}
.list a {
  display: block;
  padding: 4px;
  padding-left: 10px;
  font-size: 13px;
  line-height: 24px;
  border-bottom: 1px dashed #e2e2e2;
}
.list a:last-child {
  border-bottom: 0;
}
.ant-collapse-content-box a:hover {
  border-bottom: 1px solid #ccc;
}
.search {
  background: #fff;
}



