@charset "utf-8";
.tags {
  position: relative;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 10px;
  height: 80px;
  border: 1px dashed #ccc;
}
.tags i {
  position: absolute;
  right: 4px;
  top: 4px;
  font-size: 18px;
  display: none;
  cursor: pointer;
}
.tags:hover i {
  display: block;
}
.tags label {
  margin-left: 0 !important;
}
.color div {
  margin-bottom: 20px;
  line-height: 30px;
}
.color input[type=color] {
  display: block;
}



