@charset "utf-8";
.item {
  padding: 0 0 20px 0;
  border-bottom: 1px solid #f0f0f0;
  background-color: #fff;
  margin-bottom: 20px;
  height: 190px;
}
.item:hover {
  -webkit-box-shadow: 2px 2px 5px #B3B3B3;
          box-shadow: 2px 2px 5px #B3B3B3;
}
.item .itemLeft {
  float: left;
  padding-left: 10px;
  width: 205px;
}
.item .itemRight {
  margin-left: 225px;
}
.item .itemContent {
  line-height: 24px;
  font-size: 14px;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  height: 48px;
}
.item .itemInfo {
  line-height: 20px;
  color: #919898;
  font-size: 14px;
}
.item .itemTitle {
  margin: 0 10px 0 0;
  padding: 10px 10px 14px 10px;
  color: #00a67c;
  font-size: 20px;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  overflow: hidden;
  font-weight: 600;
  display: block;
}
.item span {
  margin-right: 30px;
}
.item a {
  color: #1d1d1f;
}
.item .tag {
  margin-right: 5px;
  position: relative;
  top: -2px;
  padding: 2px 6px 4px;
  float: left;
  font-size: 12px;
  background-color: #d9534f;
  line-height: 14px;
  color: #fff;
  margin-top: 8px;
}
.item .tag:after {
  content: "";
  position: absolute;
  z-index: 999;
  border: 5px solid #fff;
  border-left-color: #d9534f;
  left: 100%;
  top: 5px;
}
.item .titlespan {
  float: left;
  margin-left: 14px;
}
.item .hot {
  font-size: 12px;
  color: red;
  position: absolute;
  top: -10px;
}



